<template>
  <div id="index">
    <div class="lucky-wheel">
      <div class="lucky-date">
        <p>2019.5.22-5.24日广东现代国际会展中心(广东东莞厚街)</p>
        <span @click="record_btn">抽奖记录>></span>
      </div>
      <div class="lucky-title"></div>
      <div class="wheel-main">
        <div class="wheel-pointer-box">
          <div class="wheel-pointer" @click="popover"
               :style="{transform:rotate_angle_pointer,transition:rotate_transition_pointer}"></div>
        </div>
        <div class="wheel">
          <div class="wheel-bg" :style="{transform:rotate_angle,transition:rotate_transition}">
            <div class="prize-list">
              <div class="prize-item" v-for="(item,index) in prize_list" :key="index" v-if="prize_list.length>0">
                <div class="prize-pic">
                  <img :src="config.url + item.image" alt="">
                </div>
                <div class="prize-type">
                  {{item.shopname}}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="lucky-site"></div>
      <img class="qr_img" src="../assets/images/qr1.png" alt="" style="width:5rem;height:5rem">
      <div class="lucky-date">
        <p>关注上方二维码即可参与抽奖活动！</p>
      </div>
    </div>
    <!-- 二维码弹出框 -->
    <div @click="end($event)" class="popover" v-if="qr_control">
      <div class="message_box" style="background: transparent">
        <img class="qr_explain" src="../assets/images/qrexplain.png" alt="">
        <img class="qr_img" src="../assets/images/qr1.png" alt="" style="margin:5rem auto 0">
      </div>
    </div>
    <!-- 填写用户资料 -->
    <div @click="end($event)" class="popover" v-if="personal_control">
      <div class="message_box">
        <div class="message_title">
          <p>完善信息</p>
          <img src="../assets/images/messagelogo.png" alt="">
        </div>
        <ul class="from">
          <li>
            <span>姓名:</span>
            <div class="input"><input @blur="scrollTop" type="text" v-model.trim="messagefrom.name"></div>
          </li>
          <li>
            <span>邮箱:</span>
            <div class="input"><input @blur="scrollTop" type="text" v-model.trim="messagefrom.email"></div>
          </li>
          <li>
            <span>公司名称:</span>
            <div class="input"><input @blur="scrollTop" type="text" v-model.trim="messagefrom.company"></div>
          </li>
          <li>
            <span>地址:</span>
            <div class="input"><input @blur="scrollTop" type="text" v-model.trim="messagefrom.location"></div>
          </li>
          <li>
            <span>手机号:</span>
            <div class="input"><input @blur="scrollTop" type="text" v-model.trim="messagefrom.phone"></div>
          </li>
          <li>
            <span>验证码:</span>
            <div class="input vcodeBox"><input @blur="scrollTop" type="text" v-model.trim="messagefrom.verification">
            </div>
            <div class="vcode" @click="vcodeBtn">{{vcode_time}}</div>
          </li>
        </ul>
        <!-- 按钮 -->
        <div class="draw_btn" @click="tolottery">去抽奖</div>
      </div>
    </div>
    <!-- 抽奖记录record -->
    <div @click="end($event)" class="popover" v-if="record_control">
      <div class="record_box">
        <img src="../assets/images/record_title.png" alt="">
        <div class="record_text">
          <h3><img src="../assets/images/gift.png" alt="">中奖纪录</h3>
          <ul class="gift_list">
            <li v-for="item in gift_list">
              <div>
                <p>精美礼品{{item[0].shopname}}一份</p>
                <p>2019.5.23</p>
              </div>
              <span @click="to_conversion(item[0].shop_id)" v-if="item[0].status ==='0'">去兑换>></span>
              <span style="background: #414141;color:#fff;" v-else>已兑换</span>
            </li>
          </ul>
          <!-- 关闭按钮 -->
          <div class="close" @click="close">
            <img src="../assets/images/close.png" alt="">
          </div>
        </div>
      </div>
    </div>
    <!-- 兑奖 -->
    <div @click="end($event)" class="popover" v-if="toast_control">
      <div class="message_box toast" style="background: transparent">
        <img src="../assets/images/h5_03.png" alt="">
        <div>
          <p>恭喜您获得</p>
          <img :src="config.url + prize_list[index].image" alt="">
          <p v-if="prize_list.length>0">精美{{prize_list[index].shopname}}一份</p>
        </div>
        <img @click="conversion" class="toastbtn" src="../assets/images/acknow.png" alt="">
        <span @click="close"></span>
      </div>
    </div>
    <!-- 已抽过奖 -->
    <div @click="end($event)" class="popover" v-if="repeat_control">
      <div class="message_box repeat" style="background: transparent">
        <img src="../assets/images/repeat.png" alt="">
        <span @click="close"></span>
      </div>
    </div>
    <!-- 兑奖码 -->
    <div @click="end($event)" class="popover" v-if="lottery_code_control">
      <div class="message_box repeat" style="background: transparent">
        <img src="../assets/images/lottery_code.png" alt="">
        <span @click="close"></span>
        <div class="lottery_code_title">
          <p>您的兑奖码</p>
          <p>把兑奖码提供给人员确认后兑换礼品</p>
          <p>{{prize_list[index].coding}}</p>
        </div>
      </div>
    </div>
    <!-- 背景遮罩 -->
    <div class="toast-mask" v-if="control_mask"></div>
  </div>
</template>

<script>
  import config from '../config.js'

  let http = 'https://www.opjpg.com';
  export default {
    name: 'Index',
    data() {
      return {
        config: config,
        //奖品列表--------------------------------------
        prize_list: [],
        // 二维码--------------------------------------
        qr_box: false,// 是否关注二维码 false:未关注二维码
        qr_control: false, // 二维码弹出框
        // 用户信息--------------------------------------
        personal_box: false,// 是否填写用户信息 false:未填写用户信息
        personal_control: false, // 填写用户信息弹出框
        messagefrom: {
          name: '',
          email: '',
          company: '',
          location: '',
          phone: '',
          verification: '',
        }, // 个人信息
        vcode_time: '发送验证码', // 验证码按钮显示内容
        vcode_control: true, // 验证码按钮是否可点击
        fromrules: false,// 表单验证是否通过
        // 已抽过奖--------------------------------------
        repeat_box: false,// 是否已抽过奖 false:今日未抽过奖
        repeat_control: false, // 是否已抽过奖弹框控制器
        // 中奖纪录--------------------------------------
        record_control: false, // 中奖纪录
        // 兑奖--------------------------------------
        toast_control: false, //抽奖结果弹出框控制器
        // 兑奖码--------------------------------------
        lottery_code_control: false, // 兑奖码控制器
        // 遮罩--------------------------------------
        control_mask: false, //弹出框背景控制器
        // 转盘--------------------------------------
        start_rotating_degree: 0, //初始旋转角度
        rotate_angle: 0, //将要旋转的角度
        start_rotating_degree_pointer: 0, //指针初始旋转角度
        rotate_angle_pointer: 0, //指针将要旋转的度数
        rotate_transition: "transform 6s ease-in-out", //初始化选中的过度属性控制
        rotate_transition_pointer: "transform 6s ease-in-out", //初始化指针过度属性控制
        click_flag: false, //是否可以旋转抽奖
        index: 0, //指定每次旋转到的奖品下标

        btn: false,

        errList: 1,// 弹出第一个报错
        openid: '',
        gift_list: [], // 获奖记录
        giftid: [],//获奖礼品id
      }
    },
    methods: {
      // 回到顶部
      scrollTop() {
        setTimeout(function () {
          window.scrollTo(0, 0)
        }, 100)
      },
      // 准备数据
      getData() {
        this.$axios.post('http://game.iars-expo.com/demo/index/prize')
          .then(res => {
            this.prize_list = res.data.data;
            console.log(this.prize_list);
          });
        this.$axios.post('http://game.iars-expo.com/demo/index/shouye', this.$qs.stringify({
          code: document.cookie.match(/code=(.*?)(;|$)/)[1],
        }))
          .then(res => {
            console.log(res); // 获取用户身份状态-------------------------------
            this.btn = true;
            this.openid = res.data.opednid; // 获取openid
            if (res.data.code === '已关注') {
              this.qr_box = true;
            } else {
              this.qr_box = false;
            }
            if (res.data.mobile) { // 是否填写个人信息
              this.personal_box = true;
              this.messagefrom.phone = res.data.mobile;
            } else {
              this.personal_box = false;
            }
          });
      },
      // 点击弹出框判断
      popover() {
        if (!this.btn) return;
        this.btn = false;
        console.log('点击了抽奖');
        // 开始判断用户状态----------------------------------
        // 是否关注二维码
        if (!this.qr_box) {
          // 未关注二维码
          this.qr_control = true;
          this.control_mask = true;
          return;
        }
        // 是否填写用户信息
        if (!this.personal_box) {
          // 未 填写用户信息
          this.personal_control = true;
          this.control_mask = true;
          return;
        }

        // 如果 关注了二维码 并且 填写了个人信息
        // 获取已抽过的奖品
        this.$axios.post('http://game.iars-expo.com/demo/index/jilu', this.$qs.stringify({
          openid: this.openid,
        }))
          .then(res => {
            console.log('获奖记录', res);
            this.gift_list = res.data.data;
            // 判断奖品是否超出三个
            if (res.data.data.length >= 3) {
              alert('每人只能获得三个奖品')
            } else {
              // 将奖品id 传给后台判断 今日是否已抽过奖
              this.giftid = [];
              res.data.date.map((v) => {
                this.giftid.push(v.Id); // 获取已获得奖品的id '1,2,3'
              });
              // 上传已抽奖礼品id返回最近一次获得奖品的时间，判断今天是否已抽过
              this.$axios.post('http://game.iars-expo.com/demo/index/choujiang', this.$qs.stringify({
                openid: this.openid,
                shop_id: this.giftid.join(','),
              }))
                .then(res => {
                  console.log(res);
                  if (res.data.data !== 1) {
                    let reg = new RegExp('-', "g");
                    let new_gift = res.data.data.replace(reg, "/").substr(res.data.data.replace(reg, "/").length - 2, 2); // 最近获得礼物的时间
                    let now_gift = (new Date).toLocaleDateString().substr((new Date).toLocaleDateString().length - 2, 2); // 今天的时间
                    if (new_gift < now_gift) {
                      this.repeat_box = false; // 今日未抽奖
                      this.click_flag = true
                    } else {
                      this.repeat_box = true; // 今日已抽奖
                      this.click_flag = false;
                      this.repeat_control = true;
                      this.control_mask = true;
                    }
                  } else {
                    this.click_flag = true;
                    this.repeat_box = false;
                  }

                  if (this.click_flag) {
                    console.log(this.messagefrom.phone);
                    this.$axios.post('http://game.iars-expo.com/demo/index/pduser', this.$qs.stringify({
                      mobile: this.messagefrom.phone,
                    }))
                      .then(res => {
                        console.log(res);
                        this.prize_list.map((value, index) => {
                          if (value.shopname === res.data.date.yes) {
                            console.log(index);
                            this.index = index;
                            this.rotating();
                          }
                        });
                      })
                  }
                });
            }
          });
      },
      // 点击获取验证码
      vcodeBtn() {
        if (!this.vcode_control) return; // 判断验证码是否可点击
        this.tolottery_css(new RegExp(/^1[3456789]\d{9}$/).test(this.messagefrom.phone), 5);
        if (!new RegExp(/^1[3456789]\d{9}$/).test(this.messagefrom.phone)) return alert('请填写正确手机号');

        this.vcode_control = false; // 验证码按钮是否可点击
        let down = 60;
        this.vcode_time = down + 's';
        let t = setInterval(() => {
          down--;
          this.vcode_time = down + 's';
          if (down < 0) {
            clearInterval(t);
            this.vcode_time = '发送验证码';
            this.vcode_control = true;
          }
        }, 1000);
        this.$axios.post('http://game.iars-expo.com/demo/index/duanxin', this.$qs.stringify({
          mobile: this.messagefrom.phone,
        }))
          .then(res => {
            console.log(res);
            console.log(res.data.code);
            // if (res.data.code !== 'Success') {
            //   alert('验证码发送频繁 或 该号码今日提交超过限制次数');
            // }
          })
      },
      // 表单判断对错 验证样式
      tolottery_css(reg, index, aa) {
        if (reg) {
          $('.from>li:nth-child(' + index + ')>.input').css({borderBottomColor: '#d9d9d9'});
          return true;
        } else {
          $('.from>li:nth-child(' + index + ')>.input').css({borderBottomColor: 'red'});
          if (this.errList === 1) {
            alert($('.from>li:nth-child(' + index + ')>.input').prev().text().replace(":", "") + '填写有误');
            this.errList++;
          }
          return false;
        }
      },
      // 提交用户信息
      tolottery() {
        console.log('提交');
        this.errList = 1;
        let name = this.tolottery_css(this.messagefrom.name.length > 0, 1);
        let email = this.tolottery_css(new RegExp(/^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/).test(this.messagefrom.email), 2);
        let company = this.tolottery_css(this.messagefrom.company.length > 0, 3);
        let location = this.tolottery_css(this.messagefrom.location.length > 0, 4);
        let phone = this.tolottery_css(new RegExp(/^1[3456789]\d{9}$/).test(this.messagefrom.phone), 5);
        let verification = this.tolottery_css(this.messagefrom.verification.length > 0, 6);
        if (name && email && company && location && location && phone && verification) {
          this.fromrules = true;
        } else {
          this.fromrules = false;
        }
        console.log('表单验证是否成功', this.fromrules);
        if (this.fromrules) {
          this.$axios.post('http://game.iars-expo.com/demo/index/usersname', this.$qs.stringify({
            usernmae: this.messagefrom.name,
            email: this.messagefrom.email,
            company_name: this.messagefrom.company,
            mobile: this.messagefrom.phone,
            address: this.messagefrom.location,
            verify: this.messagefrom.verification,
            openid: this.openid,
          }))
            .then(res => {
              console.log(res);
              this.btn = true;
              if (res.data.msg === "添加成功") {
                this.personal_box = true;
                this.control_mask = false;
                this.personal_control = false;
                this.click_flag = true;
              } else {
                alert(res.data.msg);
              }
            });
        }
      },
      // 旋转
      rotating() {
        console.log('旋转');
        if (!this.click_flag) return; // 判断是否可以旋转抽奖
        let type = 0; // 默认为 0 = 转盘转动 1 = 箭头和转盘都转动(暂且遗留)
        let during_time = 5; // 默认为1s
        let result_index = this.index; // 最终要旋转到哪一块，对应prize_list的下标
        let result_angle = [0, -60, -120, -180, -240, -300]; //最终会旋转到下标的位置所需要的度数
        let rand_circle = 6; // 附加多转几圈，2-3
        this.click_flag = false; // 旋转结束前，不允许再次触发
        if (type === 0) {
          // 转动盘子
          // 旋转角度= 初始角度+ 至少转几圈+ 最终会旋转到下标的位置所需要的度数+
          let rotate_angle =
            this.start_rotating_degree +
            rand_circle * 360 +
            result_angle[result_index] -
            this.start_rotating_degree % 360;
          this.start_rotating_degree = rotate_angle;
          this.rotate_angle = "rotate(" + rotate_angle + "deg)";
          //转动指针
          // this.rotate_angle_pointer = "rotate("+this.start_rotating_degree_pointer + 360*rand_circle+"deg)";
          // this.start_rotating_degree_pointer =360*rand_circle;
          let that = this;
          // 旋转结束后，允许再次触发
          setTimeout(function () {
            that.click_flag = true;

            that.game_over();
          }, during_time * 1000 + 1500); // 延时，保证转盘转完
        } else {
          //
        }
      },

      // 转盘停止旋转后执行
      game_over() {
        console.log('game_over');
        this.toast_control = true;
        this.control_mask = true;
      },
      // 抽奖记录
      record_btn() {
        if (!this.btn) return;
        this.record_control = true;
        this.control_mask = true;
        this.$axios.post('http://game.iars-expo.com/demo/index/jilu', this.$qs.stringify({
          openid: this.openid,
        }))
          .then(res => {
            console.log('获奖记录', res);
            this.gift_list = res.data.data;
          })
      },
      // 点击抽奖记录去兑换
      to_conversion(id) {
        this.record_control = false;
        this.toast_control = true;
        console.log(this.prize_list);
        this.prize_list.map((value,index)=>{
          console.log(value);
          console.log(value.Id);
          console.log(id);
          if(value.Id == id){
            this.index = index;
          }
        });
        console.log(this.index);
      },
      // 关闭抽奖记录
      close() {
        this.record_control = false;
        this.repeat_control = false;
        this.lottery_code_control = false;
        this.control_mask = false; // 遮罩
        this.toast_control = false;
        this.btn = true;
      },
      //  点击兑换
      conversion() {
        this.toast_control = false;
        this.lottery_code_control = true;
        this.btn = true;
        console.log(this.btn);
      },
      // 关闭所有弹窗
      end(e) {
        if (e.target.className === 'popover') {
          this.qr_control = false; // 二维码关闭
          this.personal_control = false;// 个人信息关闭
          this.repeat_control = false; // 是否已抽过奖弹框控制器 关闭
          this.record_control = false; // 中奖纪录框关闭
          this.toast_control = false; // 抽奖结果弹出框 关闭
          this.lottery_code_control = false; // 兑奖码控制器 关闭
          this.control_mask = false; // 遮罩关闭
          this.btn = true;
        }
      },
      listener() {  // 监听是否关注
        if (this.qr_control) {
          window.location.href = 'http://www.opjpg.com';
        }
      },
    },
    mounted() {
      //  准备数据
      this.getData();
      window.addEventListener('focus', this.listener);
      document.documentElement.addEventListener('touchstart', function (event) {
        if (event.touches.length > 1) {
          event.preventDefault();
        }
      }, false);
    },
    destroyed() {
      window.removeEventListener('focus', this.listener);
    },
    updated() {
      if(this.control_mask){
        var top=$(document).scrollTop();
        $(document).on('scroll.unable',function (e){
          $(document).scrollTop(top);
        })
      }else{
        $(document).unbind("scroll.unable");
      }
    }
  }
</script>

<style scoped>
  #index {
    height: 100%;
    background: #a40000;
  }

  .lucky-wheel {
    overflow: hidden;
    width: 100%;
    background: url("~@/assets/images/bg.jpg") no-repeat center;
    background-size: 100% 100%;
    padding: 1rem 0 2rem;
  }

  .lucky-date {
    font-size: 0.7rem;
    text-align: center;
    color: #fff;
    position: relative;
  }

  .lucky-date > span {
    float: right;
    margin-top: 0.5rem;
    margin-right: 1rem;
    color: #fdf338;
  }

  .lucky-title {
    width: 100%;
    height: 14rem;
    background: url("~@/assets/images/title.png") no-repeat center;
    background-size: 85%;
  }

  .lucky-site {
    width: 100%;
    height: 3rem;
    background: url("~@/assets/images/site.png") no-repeat center;
    background-size: 75%;
    margin-top: -1.3rem;
  }

  .qr_img {
    width: 7rem;
    height: 7rem;
    display: block;
    margin: 1rem auto;
  }

  .qrBox2 > li {
    width: 9.5625rem;
    height: 9.5625rem;
    margin: 0 1rem;
  }

  .wheel-main {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    transform: scale(0.9);
    margin-top: -4.5rem;
  }

  .wheel {
    width: 22rem;
    height: 22rem;
    background: url("~@/assets/images/dish.png") no-repeat center;
    background-size: 100% auto;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }

  .wheel-bg {
    width: 20rem;
    height: 20rem;
    background: url("~@/assets/images/tray.png") no-repeat center;
    background-size: 100% auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    transition: transform 3s ease;
  }

  .wheel-pointer-box {
    position: absolute;
    top: 51%;
    left: 50%;
    z-index: 100;
    transform: translate(-50%, -60%);
    width: 5rem;
    height: 6rem;
  }

  .wheel-pointer {
    width: 5rem;
    height: 6rem;
    background: url("~@/assets/images/lotteryBtn.png") no-repeat center;
    background-size: 100%;
    transform-origin: center 60%;
  }

  .wheel-bg div {
    text-align: center;
  }

  .prize-list {
    width: 100%;
    height: 100%;
    position: relative;
  }

  .prize-list .prize-item {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
  }

  .prize-list .prize-item:first-child {
    top: 0;
    left: 7.1rem;
    transform: rotate(0deg);
  }

  .prize-list .prize-item:nth-child(2) {
    top: 2.8rem;
    left: 11.8rem;
    transform: rotate(60deg);
  }

  .prize-list .prize-item:nth-child(3) {
    top: 8.2rem;
    left: 11.8rem;
    transform: rotate(120deg);
  }

  .prize-list .prize-item:nth-child(4) {
    top: 11rem;
    left: 7.1rem;
    transform: rotate(180deg);
  }

  .prize-list .prize-item:nth-child(5) {
    top: 8.3rem;
    left: 2.3rem;
    transform: rotate(240deg);
  }

  .prize-list .prize-item:nth-child(6) {
    top: 2.8rem;
    left: 2.3rem;
    transform: rotate(300deg);
  }

  .prize-item {
    width: 5.875rem;
    height: 9rem;
  }

  .prize-pic img {
    width: 2.6rem;
    height: 2.6rem;
    margin-top: 2.3rem;
    border-radius: 50%;
    border: 4px solid #fbe8c0;
  }

  .prize-type {
    font-size: 0.75rem;
  }

  .popover {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10001;
  }

  .qr_explain {
    width: 80%;
    display: block;
    margin: 0 auto;
  }

  .message_box {
    background: #fff;
    width: 18rem;
    height: 28rem;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    border-radius: 1rem;
    overflow: hidden;
  }

  .message_title {
    width: 100%;
    background: linear-gradient(to right, #ecbd4f, #dd7741);
    text-align: center;
    padding-top: 1.5rem;
  }

  .message_title > p {
    color: #fff;
    text-align: center;
    margin-bottom: 0.8rem;
  }

  .message_title > img {
    width: 50%;
    vertical-align: bottom;
  }

  .from > li {
    text-align: center;
    margin: 0.75rem 0;
  }

  .from > li > span {
    display: inline-block;
    width: 4rem;
    text-align: left;
    font-size: 0.875em;
    text-size-adjust: none;
    color: #404040;
  }

  .from > li input {
    border: none;
    padding: 0 0 0 0.3125rem;
    font-size: 0.875em;
    width: 100%;
    text-size-adjust: none
  }

  .from > li > .vcode {
    border: 1px solid #f85446;
    display: inline-block;
    padding: 0.1rem 0.2rem;
    border-radius: 0.2rem;
    color: #f85446;
    width: 5rem;
    font-size: .875em;
    text-size-adjust: none
  }

  .from > li > .vcodeBox {
    width: 4.5rem !important;
    text-size-adjust: none
  }

  .from > li > .input {
    display: inline-block;
    border-bottom: 1px solid #d9d9d9;
    width: 10rem;
    text-size-adjust: none
  }

  .draw_btn {
    width: 50%;
    color: #fff;
    background: linear-gradient(to right, #ecbd4f, #dd7741);
    border-radius: 1rem;
    text-align: center;
    padding: 0.3rem 0;
    margin: 1.5rem auto 0;
  }

  .record_box {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 18rem;
    height: 28rem;
  }

  .record_box > img {
    width: 100%;
    vertical-align: bottom;
  }

  .record_text {
    margin: 0 0.46rem 0 0.48rem;
    background: #fff;
    border-radius: 0 0 1rem 1rem;
    padding-top: 1rem;
    position: relative;
    z-index: 1;
  }

  .close {
    width: 2.5rem;
    position: absolute;
    bottom: -4.5rem;
    left: 0;
    right: 0;
    margin: auto;
  }

  .close > img {
    width: 100%;
  }

  .record_text > h3 {
    text-align: center;
    font-weight: normal;
    font-size: 1.25rem;
  }

  .record_text > h3 > img {
    height: 1.6rem;
    vertical-align: middle;
    margin-right: 0.3rem;
  }

  .gift_list {
    padding: 1rem 1.5rem;
    height: 9.5rem;
  }

  .gift_list > li {
    margin: 0.5rem 0;
    overflow: hidden;
  }

  .gift_list > li > div {
    float: left;
    width: 70%;
    font-size: 0.875rem;
  }

  .gift_list > li > div > p:nth-child(2) {
    color: #999;
  }

  .gift_list > li > span {
    float: right;
    display: inline-block;
    height: 2rem;
    width: 30%;
    background: #fcca05;
    font-size: 0.875rem;
    text-align: center;
    line-height: 2rem;
    border-radius: 0.3rem;
    color: #ff0000;
  }

  .toast-mask {
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.74);
    z-index: 10000;
    width: 100%;
    height: 100%;
  }

  .toast > img {
    width: 100%;
  }

  .toast > span {
    display: block;
    width: 2rem;
    height: 2rem;
    background: url("~@/assets/images/close_01.png") no-repeat center;
    background-size: 100%;
    margin: 4rem auto 0;
  }

  .toast > div {
    position: absolute;
    top: 0;
    margin-left: 50%;
    transform: translateX(-50%);
    color: #fff;
    padding-top: 5rem;
  }

  .toast > div > p {
    text-align: center;
  }

  .toast > div > p:nth-of-type(2) {
    font-size: 0.875rem;
  }

  .toast > div > img {
    display: block;
    width: 5rem;
    height: 5rem;
    border-radius: 8rem;
    margin: 0.3125rem auto;
    border: 0.25rem solid #fbe8c0;
  }

  .toastbtn {
    position: absolute;
    left: 0;
    right: 0;
    top: 19rem;
    margin: auto;
    width: 75% !important;
  }

  .repeat > img {
    width: 100%;
  }

  .repeat > span {
    display: block;
    width: 2rem;
    height: 2rem;
    background: url("~@/assets/images/close_01.png") no-repeat center;
    background-size: 100%;
    margin: 6.5rem auto 0;
  }

  .lottery_code_title {
    position: absolute;
    top: 10rem;
    width: 100%;
    font-size: 1rem;
  }

  .lottery_code_title > p {
    text-align: center;
  }

  .lottery_code_title > p:nth-child(1) {
    font-weight: bold;
  }

  .lottery_code_title > p:nth-child(2) {
    color: #999;
    font-size: 0.75rem;
    margin: 0.5rem 0 2rem;
  }

  .lottery_code_title > p:nth-child(3) {
    font-weight: bold;
    color: #ff0000;
  }

</style>



