import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'

Vue.use(Router);

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      beforeEnter: (to, from, next) => {
        console.log(Boolean(!to.query.code));
          if(!to.query.code){
            window.location.href ='https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxc512b8496a77a2d8&redirect_uri='+encodeURIComponent('http://game.iars-expo.com')+'&response_type=code&scope=snsapi_userinfo&state=STATE&connect_redirect=1#wechat_redirect'
            console.log('luyou内');
          }else{
            console.log(to.query.code);
            var expirationTime = new Date();
            expirationTime.setMinutes(expirationTime.getMinutes() + 60);//设置过期时间
            document.cookie = "code=" + to.query.code + ";expires=" + expirationTime.toUTCString();
            next();
          }
      }
    }
  ]
})



